<template>
    <main>
        <h1>{{ test }}</h1>
        <button @click="getAlgo()"></button>
    </main>
</template>

<script>
import axios from 'axios';
import {mapState} from 'vuex';

export default {

    data () {

        return {
            test: 'hola'
        }

    },

    methods: {

        getAlgo () {

            axios.get('api/algo')
                .then(function (response) {
                    console.log(response);
                })
                .catch(function (error) {
                    console.log(error);
                });

        }

    }

}
</script>

<style lang="scss"></style>



